








import Vue from 'vue';
import {
  Component,
  Prop,
} from 'vue-property-decorator';
import InlineSvg from 'vue-inline-svg';

@Component({
  name: 'cp-icon',
  components: {
    InlineSvg,
  },
})
export default class CpIcon extends Vue {
  /**
   * Props
   */
  @Prop({ type: String, required: true })
  public name: string;

  @Prop({ type: String, default: '' })
  public title: string;
}
